import React from "react";
import { Tabs, Tab, CircularProgress, Typography } from "@material-ui/core";
import Informacion from "./Informacion";
import Cobertura from "./Cobertura";
import Horarios from "./Horarios";
import API from "../../Services/API";

export default class extends React.PureComponent {
    state = {
        // toda la configuracion
        currentTab: 0,
        defaultConfiguracion: null,
        loading: true,
        error: null,
        // aqui almaceno los Tabs que ya se han cargado.para no cargar todo de golpe!
        LoadedTabs: {}
    }
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepID);
        this.handleTabChange = this.handleTabChange.bind(this);
    }
    componentWillMount() {
        API.GetConfigInfo()
            .then(configuracion => {
                this.setState(prev => {
                    const state = { ...prev };
                    state.configuracion = configuracion;
                    state.loading = null;
                    state.LoadedTabs[state.currentTab] = true;
                    return state;
                });
            }).catch(error => this.setState({ error, loading: null }));
    }
    handleTabChange(_, currentTab) {
        this.setState(prev => {
            const state = { ...prev };
            state.LoadedTabs[currentTab] = true;
            state.currentTab = currentTab;
            return state;
        });
    }

    render() {
        const { currentTab, LoadedTabs, error, loading, configuracion } = this.state;
        if (loading)
            return (<div style={{ width: "100%", textAlign: "center", paddingTop: 100 }}>
                <CircularProgress size={100} />
                <Typography variant="h4" >CARGANDO...</Typography>
            </div>);

        if (error)
            return (<div style={{ width: "100%", textAlign: "center", paddingTop: 200 }}>
                <Typography variant="h6" >Ocurrió un error al cargar tu configuración</Typography>
                <Typography variant="subtitle1">{error.message}</Typography>
                <Typography variant="body1" >{error.code} {error.status}</Typography>
            </div>);

        return (<div style={{ display: "flex", flexDirection: "column", height: "100%" }}>
            <Tabs variant="fullWidth" onChange={this.handleTabChange} value={currentTab} indicatorColor="primary" textColor="primary">
                <Tab label="Información" />
                <Tab label="Cobertura y Tarifas" />
                <Tab label="Horarios de Atención" />
            </Tabs>
            <div style={{ display: currentTab === 0 ? "block" : "none", flex: 1 }}>
                {LoadedTabs[0] && <Informacion configuracion={configuracion} />}
            </div>

            <div style={{ display: currentTab === 1 ? "block" : "none", flex: 1 }}>
                {LoadedTabs[1] && <Cobertura configuracion={configuracion} />}
            </div>

            <div style={{ display: currentTab === 2 ? "block" : "none", flex: 1 }}>
                {LoadedTabs[2] && <Horarios configuracion={configuracion} />}
            </div>
        </div>);
    }
}