export function ReadImgAsUrl(files) {
    return new Promise((resolve, reject) => {
        var file = files && files[0];
        if (!file) return reject({ message: "Archivo no encontrado" });
        if (file.type.indexOf("image") < 0)
            return reject({ message: "El archivo no es una imagen" });
        var bytes = file.size;
        var mbs = (bytes * 9.5367431640625e-7).round(2);
        if (mbs > 1) {
            reject({ message: `Solo se pueden subir imagenes de hasta 1mb. Umagen actual (${mbs} MB)` })
        }
        if (files && files[0]) {
            var reader = new FileReader();
            reader.onload = function (e) {
                resolve(e.target.result);
            };
            reader.onerror = reject;
            reader.readAsDataURL(file);
        }
    });
}