import React from "react";
import { Grid } from "@material-ui/core";
import API from "../../Services/API";
import UploadImage from "../UploadImage";
import { ReadImgAsUrl } from "../../Services/FileReader";
import UpdateableInput from "./UpdateableInput";

export default class extends React.PureComponent {
    state = {
        // {key : "cargando" | errMessage}
        feedback: {}
    }
    // aqui almaceno los timeouts
    timeouts = {}
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepID);
        this.handleInput = this.handleInput.bind(this);
        this.handleUpdateProp = this.handleUpdateProp.bind(this);
        this.handleLogo = this.handleLogo.bind(this);
        const { configuracion } = props;
        if (configuracion) {
            for (const key in configuracion) {
                if (configuracion.hasOwnProperty(key)) {
                    this.state[key] = configuracion[key];
                }
            }
        }
    }

    handleInput(event) {
        const { name, value } = event.currentTarget;
        if (this.state.feedback[name] === "loading") return;
        this.setState({
            [name]: value
        });
    }
    handleUpdateProp(name, value, avoidCatch) {
        if (this.state.feedback[name]) return;
        this.setState(prev => {
            const state = { ...prev };
            state.feedback = { ...state.feedback, [name]: "loading" };
            return state;
        });
        var promise = API.setConfigInfo({
            [name]: value || this.state[name]
        }).then(x =>
            this.setState(prev => {
                const state = { ...prev };
                state.feedback = { ...state.feedback, [name]: "loading" };
                delete state.feedback[name];
                return state;
            })
        )
        if (avoidCatch) return promise;
        promise.catch(err =>
            this.setState(prev => {
                const state = { ...prev };
                state.feedback = { ...state.feedback, [name]: err.message };
                return state;
            })
        );
    }
    handleLogo(event) {
        if (this.state.feedback.logoSrc === "loading") return;
        ReadImgAsUrl(event.currentTarget.files)
            .then(logoSrc => this.handleUpdateProp("logoSrc", logoSrc, true)
                .then(x => this.setState({ logoSrc }))
            ).catch(ex => {
                alert("Ocurrio un problema al cargar tu imagen: " + ex.message);
                this.setState(prev => {
                    const state = { ...prev };
                    state.feedback = { ...state.feedback };
                    delete state.feedback.logoSrc;
                    return state;
                });
            });
    }

    render() {
        const { titulo, slogan, logoSrc,
            direccion, telefono,
            whatsAppMessage, whatsAppPhone,
            feedback } = this.state;
        return (<div>
            <Grid container>
                <Grid item xs={6} style={{ padding: 10 }}>

                    <UpdateableInput
                        label="Nombre de la Tienda"
                        onUpdate={this.handleUpdateProp}
                        value={titulo || ""}
                        name={"titulo"}
                        onChange={this.handleInput}
                        feedback={feedback}
                    />
                    <div style={{ textAlign: "center", padding: 10 }}>
                        <UploadImage
                            loading={feedback.logoSrc}
                            onChange={this.handleLogo}
                            src={logoSrc}
                            border={true}
                            name="Logo de la Tienda"
                            height="calc(100vh - 400px)"
                        />
                    </div>

                    <UpdateableInput
                        label="Slogan de la Tienda"
                        onUpdate={this.handleUpdateProp}
                        value={slogan || ""}
                        name={"slogan"}
                        onChange={this.handleInput}
                        feedback={feedback}
                    />
                </Grid>
                <Grid item xs={6} style={{ padding: 10 }}>

                    <UpdateableInput
                        label="Direccion de la Tienda"
                        onUpdate={this.handleUpdateProp}
                        value={direccion || ""}
                        name={"direccion"}
                        onChange={this.handleInput}
                        feedback={feedback}
                        multiline={true}
                        inputProps={{ rows: 2 }}
                    />

                    <UpdateableInput
                        label="Telefono"
                        onUpdate={this.handleUpdateProp}
                        value={telefono || ""}
                        name={"telefono"}
                        onChange={this.handleInput}
                        feedback={feedback}
                        type="phone"
                    />

                    <UpdateableInput
                        label="Numero en Whatsapp"
                        onUpdate={this.handleUpdateProp}
                        value={whatsAppPhone || ""}
                        name={"whatsAppPhone"}
                        onChange={this.handleInput}
                        feedback={feedback}
                        type="phone"
                    />

                    <UpdateableInput
                        label="Escribe el mensaje de Whatsapp que quieres recibir cuando seas contactado"
                        onUpdate={this.handleUpdateProp}
                        value={whatsAppMessage || ""}
                        name={"whatsAppMessage"}
                        onChange={this.handleInput}
                        feedback={feedback}
                        multiline={true}
                        inputProps={{ rows: 3 }}
                    />
                </Grid>
            </Grid>
        </div>)
    }
}