import React from "react";
import {
    Card,
    CardContent,
    TextField,
    Typography,
    InputAdornment,
    Button,
    CircularProgress,
    IconButton
} from "@material-ui/core";
import { AccountCircle, Visibility, VisibilityOff } from "@material-ui/icons";
import API from "./Services/API";

const styles = {
    control: {
        marginTop: 10,
        marginBottom: 10
    }
}

export default class extends React.Component {
    state = {
        user: "",
        pdw: "",
        showPassword: false,
        loading: false,
        store: null
    }
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleTogglePassword = this.handleTogglePassword.bind(this);
    }
    handleChange(event) {
        const { name, value } = event.target;
        this.setState({ [name]: value });
    }
    handleSubmit(event) {
        event.preventDefault();
        const { loading, user, pwd } = this.state;
        if (loading) return;
        if (!user) return this.setState({ error: { message: "Digite el usuario" } });
        if (!pwd) return this.setState({ error: { message: "Digite la contraseña" } });
        this.setState({ loading: true });
        API.LogIn({
            user: user,
            pwd: pwd,
            withStoresInfo: true
        }).then(userInfo =>
            this.props.onLogged({
                user,
                token: userInfo.token,
                store: userInfo.store
            })
        ).catch(error => this.setState({ error, loading: false }));
    }
    handleTogglePassword() {
        this.setState(prev => {
            const state = { ...prev };
            state.showPassword = !state.showPassword;
            return state;
        });
    }
    render() {
        const { showPassword, loading, error, user, pwd } = this.state;
        return (<div style={{ padding: 40, overflow: "auto" }}>
            <Card style={{ width: 400, margin: "auto" }}>
                <CardContent style={{ padding: 0 }}><form onSubmit={this.handleSubmit} style={{ padding: 20 }}>
                    <Typography style={{ textAlign: "center" }} variant="h5">INICIO DE SESION</Typography>
                    <TextField
                        autoFocus={true}
                        disabled={loading}
                        required
                        style={{ ...styles.control }}
                        fullWidth
                        label="USUARIO"
                        value={user || ""}
                        name={"user"}
                        onChange={this.handleChange}
                        type="email"
                        InputProps={{
                            startAdornment: (
                                <InputAdornment position="start">
                                    <AccountCircle />
                                </InputAdornment>
                            ),
                        }}
                    />
                    <TextField
                        required
                        fullWidth
                        style={{ ...styles.control }}
                        value={pwd || ""}
                        label="CONTRASEÑA"
                        name={"pwd"}
                        onChange={this.handleChange}
                        type={showPassword ? "text" : "password"}
                        InputProps={{
                            endAdornment: (
                                <InputAdornment position="end">
                                    <IconButton onClick={this.handleTogglePassword}>
                                        {showPassword ? <Visibility /> : <VisibilityOff />}
                                    </IconButton>
                                </InputAdornment>
                            ),
                        }}
                    />
                    {error && <div style={{ width: "100%" }}>
                        <Typography color="secondary" variant="body2">{error.message} {error.code && error.code}</Typography>
                        <Typography color="secondary" variant="body1">{error.status} | {error.statusText}</Typography>
                    </div>}
                    <Button
                        type="submit"
                        fullWidth style={{ ...styles.control }}
                        variant="outlined"
                        disabled={loading}
                        color="primary"
                    >
                        {loading ? <CircularProgress size={20} color="inherit" /> : "ENTRAR"}
                    </Button>
                </form>
                </CardContent>
            </Card>
        </div >)
    }
}

