import React from "react";
import {
    Typography, IconButton,
    Card, CardActionArea, CardMedia, CardContent, CardActions,
    CircularProgress
} from "@material-ui/core";
import {
    Pause, PlayArrow, ArrowLeft, ArrowRight
} from "@material-ui/icons";

export default function (props) {
    const paused_color = props.paused ? "secondary" : "primary";
    return (<Card
        style={{ display: "inline-block", width: 200, margin: 10, height: 285 }}
        id={"ITEM-CONTAINER-" + props.id}
    >
        <CardActionArea style={{ height: 220, overflow: "hidden" }} value={props.id} onClick={props.onItemClick}>
            <CardMedia style={{ height: 140 }} image={props.imgSrc || "https://diegopq95.github.io/img/default.png"} title="Imagen"></CardMedia>
            <CardContent>
                <Typography variant="subtitle2">{props.nombre}</Typography>
                <Typography variant="body1" style={{ whiteSpace: "pre-wrap" }} >{props.descripcion}</Typography>
            </CardContent>
        </CardActionArea>
        <CardActions style={{ display: "flex", flexDirection: "row" }}>
            <IconButton
                onClick={props.onItemAction}
                disabled={props.loading === "pause"}
                name="pause"
                value={props.id}
                color={paused_color}
                title={props.paused ? "No Disponible" : "Disponible"}
            >
                {props.loading === "pause"
                    ? <CircularProgress size={20} color={paused_color} />
                    : (props.paused
                        ? <Pause fontSize="small" color="inherit" />
                        : <PlayArrow fontSize="small" color="inherit" />)
                }
            </IconButton>
            <div style={{ flex: 1, textAlign: "right" }}>
                {(props.loading === "left" || props.loading === "right")
                    ? <CircularProgress size={20} style={{ marginRight: 40 }} />
                    : (<React.Fragment>
                        <IconButton onClick={props.onItemAction} value={props.id} name="left" >
                            <ArrowLeft />
                        </IconButton>
                        <IconButton onClick={props.onItemAction} value={props.id} name="right" >
                            <ArrowRight />
                        </IconButton>
                    </React.Fragment>)}
            </div>
        </CardActions>
    </Card>)
}