import React from "react";
import { HashRouter, Switch, Route } from "react-router-dom";
import { Typography, Paper } from "@material-ui/core";
import { Inbox, Category, Settings, Assessment } from "@material-ui/icons";
import AppBar from "./Layouts/AppBar";
import AppMenu from "./Layouts/AppMenu";
import Orders from "./Components/Orders";
import API from "./Services/API";
import Notifications from "./Services/Notifications";
import Products from "./Components/Products";
import Config from "./Components/Config";

const STEPS = [
  { id: "orders", title: "Pedidos", Icon: Inbox },
  { id: "products", title: "Productos", Icon: Category },
  //{ id: "dashboard", title: "Dashboard", Icon: Assessment },
  { id: "config", title: "Configuración", Icon: Settings, addDividerBefore: true }
];
export default class extends React.Component {
  state = {
    current_step: ""
  }
  constructor(props) {
    super(props);
    this.handleMounted = this.handleMounted.bind(this);
    API.storeID = props.store.nombre;
    API.START_LISTENING();
  }
  componentDidMount() {
    if (!window.Notification) {
      console.log('Browser does not support notifications.');
    } else {
      // check if permission is already granted
      if (Notification.permission === 'granted') {
        this.REMOVE_LISTENER = Notifications.ListenOrderChanges();
      } else {
        // request permission from user
        Notification.requestPermission().then(function (p) {
          if (p === 'granted') {
            this.REMOVE_LISTENER = Notifications.ListenOrderChanges();
          } else {
            console.log('User blocked notifications.');
          }
        }).catch(function (err) {
          console.error(err);
        });
      }
    }
  }
  componentWillUnmount() {
    this.REMOVE_LISTENER && this.REMOVE_LISTENER();
  }
  handleMounted(current_step) {
    this.setState({ current_step });
  }
  render() {
    const { current_step } = this.state;
    const { title } = current_step && STEPS.find(x => x.id === current_step);
    return (<HashRouter>
      <Paper style={{ height: "100vh", width: "100%" }}>
        <AppBar user={this.props.user} store={this.props.store} title={title} onLogOut={this.props.onLogOut} />
        <AppMenu STEPS={STEPS} current_step={current_step} />
        <div style={{ height: "calc(100% - 60px)", width: "calc(100% - 205px)", paddingLeft: 205, paddingTop: 60, overflow: "hidden" }}>
          <Switch>

            <Route path="/orders" render={props => (<Orders
              {...props}
              store={this.props.store}
              onMounted={this.handleMounted}
              stepID={"orders"}
            />)} />

            <Route path="/products" render={props => (<Products
              {...props}
              store={this.props.store}
              onMounted={this.handleMounted}
              stepID={"products"}
            />)} />

            <Route path="/config" render={props => (<Config
              {...props}
              onMounted={this.handleMounted}
              stepID={"config"}
            />)} />

            <Route exact path="/" >
              <div style={{ paddingTop: 100, width: "100%", textAlign: "center" }}>
                <Typography variant="h4">BIENVENIDO!</Typography>
                <Typography variant="subtitle2">RunFood Orders - Administrador</Typography>
              </div>
            </Route>
          </Switch>
        </div>
      </Paper>
    </HashRouter>)
  }
}