import React from "react";
import {
    Dialog, DialogContent, TextField, FormControlLabel, Switch,
    Button, CircularProgress, Checkbox, Typography, InputAdornment,
    DialogActions, withStyles
} from "@material-ui/core";
import API from "../../Services/API";
import { ReadImgAsUrl } from "../../Services/FileReader";
import UploadImage from "../UploadImage";

export default withStyles({
    paper: {
        maxHeight: "calc(100% - 20px)"
    }
})(class extends React.PureComponent {
    state = {
        id: null,
        idGrupo: null,
        nombre: "",
        descripcion: "",
        pvp: "",
        imgSrc: "",
        pagaIva: false,
        habilitado: true,

        error: null,
        loading: "INITIAL"
    }
    constructor(props) {
        super(props);
        this.handleInput = this.handleInput.bind(this);
        this.handleCheck = this.handleCheck.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleDelete = this.handleDelete.bind(this);

        this.handlePhotoChange = this.handlePhotoChange.bind(this);
        this.handlePhotoDrop = this.handlePhotoDrop.bind(this);
        this.handlePhotoOver = this.handlePhotoOver.bind(this);

        const { info } = this.props.match.params;
        if (info.indexOf("~") >= 0) {
            var [id, idGrupo] = info.split("~");
            this.state.id = id;
            this.state.idGrupo = +idGrupo;
        } else {
            this.state.id = info;
        }
        if (this.state.id === "NUEVO") this.state.loading = false;
    }
    componentWillMount() {
        if (this.state.id === "NUEVO") return;
        API.GetProduct(this.state.id).then(item => {
            this.setState({ ...item, loading: false });
        }).catch(error => this.setState({ error, loading: "INITIAL-FAILED" }));
    }
    handleInput(event) {
        const { name, value } = event.currentTarget;
        this.setState({ [name]: value });
    }
    handleCheck(event) {
        const { name, checked } = event.currentTarget;
        this.setState({
            [name]: checked
        });
    }
    handleClick() {
        if (this.state.loading) return;
        if (!(this.state.pvp && !isNaN(this.state.pvp))) return alert("Digite un Precio valido");
        if (!this.state.nombre) return alert("Agregue el nombre");
        if (!this.state.idGrupo) return alert("Seleccione un grupo");
        if (!this.state.imgSrc) {
            var confirm = window.confirm("Este item no tiene una imagen, está seguro de continuar?");
            if (!confirm) return;
        }

        this.setState({ loading: true, error: null });
        API.SaveProduct(this.state)
            .then(x => {
                try {
                    this.setState({ loading: false, error: null });
                    this.props.onClose({ ...this.state, ...x });
                } catch (ex) { }
            }).catch(error => this.setState({ error, loading: false }));
    }
    handleDelete() {
        if (this.state.loading) return;
        var confirmed = window.confirm("Esta seguro de eliminar este registro?");
        if (!confirmed) return;
        this.setState({ loading: true, error: null });
        API.DeleteProduct(this.state)
            .then(x => {
                this.props.onClose({
                    id: this.state.id,
                    delete: true
                })
            }).catch(error =>
                this.setState({ error, loading: false })
            );
    }
    handleClose(event) {
        if (this.state.loading && event) return event.preventDefault();
        this.props.onClose();
    }
    handlePhotoChange(event) {
        ReadImgAsUrl(event.currentTarget.files)
            .then(imgSrc => this.setState({ imgSrc }))
            .catch(ex => alert("Ocurrio un problema al cargar tu imagen: " + ex.message));
    }

    handlePhotoDrop(event) {
        event.preventDefault();
        ReadImgAsUrl(event.dataTransfer.files)
            .then(imgSrc => this.setState({ imgSrc }))
            .catch(ex => alert("Ocurrio un problema al cargar tu imagen: " + ex.message));
        var div = document.getElementById("product-dialog-container");
        div.classList.remove("with-drop-view");
    }
    handlePhotoEnter(e) {
        var div = document.getElementById("product-dialog-container");
        div.classList.add("with-drop-view");
        e.preventDefault();
    }
    handlePhotoLeave(e) {
        e.preventDefault();
    }
    handlePhotoOver(e) {
        e.dataTransfer.dropEffect = "copy";
        e.preventDefault();
        if (this.timeout !== undefined) clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
            var div = document.getElementById("product-dialog-container");
            div.classList.remove("with-drop-view")
        }, 200);
        console.info("dragenter");
    }

    render() {
        const { state } = this;
        console.info("product dialog rendered");
        let pvp = isNaN(this.state.pvp) ? 0 : +this.state.pvp;
        const precioFinal = pvp
            ? (pvp + (state.pagaIva
                ? ((pvp * (this.props.porcentajeIVA || 12)) / 100)
                : 0)).round(2)
            : 0;

        if (state.loading === "INITIAL")
            return (<Dialog open={true}>
                <DialogContent style={{ textAlign: "center", paddingTop: 150, width: 500, height: 300 }}>
                    <CircularProgress size={70} />
                    <Typography variant="h6">CARGANDO...</Typography>
                </DialogContent>
            </Dialog>);

        if (state.loading === "INITIAL-FAILED")
            return (<Dialog onClose={event => this.handleClose()} open={true}>
                <DialogContent style={{ textAlign: "center", paddingTop: 150, width: 500, height: 300 }}>
                    <Typography color="secondary" variant="h5">Hubo un error al cargar la información</Typography>
                    <Typography color="secondary" variant="h6">{state.error.message}</Typography>
                    <Typography color="secondary" variant="body1">{state.error.code} {state.error.status}</Typography>
                </DialogContent>
            </Dialog>);


        return (<Dialog
            id="product-dialog-container"
            onDrop={this.handlePhotoDrop}
            onDragOver={this.handlePhotoOver}
            onDragEnter={this.handlePhotoEnter}
            onDragLeave={this.handlePhotoLeave}
            open={true}
            onClose={this.handleClose}
            classes={{
                paper: this.props.classes.paper
            }}
        >
            <DialogContent style={{ textAlign: "center", paddingTop: 0, paddingBottom: 0, overflowX: "hidden" }}>
                <TextField
                    autoFocus={true}
                    value={state.nombre}
                    name="nombre"
                    onChange={this.handleInput}
                    label="Nombre"
                    variant="standard"
                    fullWidth
                    style={style.textField}
                />
                <UploadImage
                    onChange={this.handlePhotoChange}
                    src={state.imgSrc}
                    name={state.nombre}
                    height={"calc(100vh - 430px)"}
                />
                <TextField
                    value={(state.descripcion === undefined || state.descripcion === null)
                        ? ""
                        : state.descripcion}
                    name="descripcion"
                    onChange={this.handleInput}
                    inputProps={{
                        rows: 3
                    }}
                    label="Descripcion"
                    variant="outlined"
                    multiline
                    fullWidth
                    style={style.textField}
                />

                <div style={{ display: "flex", flexDirection: "row" }}>
                    <TextField
                        value={state.pvp}
                        name="pvp"
                        type="number"
                        label="Precio sin impuestos"
                        inputProps={{
                            step: "0.01"
                        }}
                        onChange={this.handleInput}
                        style={{ ...style.textField, flex: 1 }}
                        InputProps={{
                            endAdornment: <InputAdornment position="end">
                                <FormControlLabel
                                    control={<Checkbox
                                        color="primary"
                                        name="pagaIva"
                                        checked={state.pagaIva}
                                        onChange={this.handleCheck}
                                    />}
                                    label="IVA"
                                />
                            </InputAdornment>
                        }}
                    />
                    <Typography variant="body2" style={{ marginLeft: 15, paddingTop: 15, width: 130, paddingLeft: 5, paddingRight: 5 }}>
                        PRECIO FINAL: ${precioFinal.toFixed(2)}
                    </Typography>
                </div>

                <div style={{ display: "flex", flexDirection: "row" }}>
                    <TextField
                        style={{ ...style.textField, flex: 1 }}
                        value={state.idGrupo || 1}
                        onChange={this.handleInput}
                        name="idGrupo"
                        label="Grupo"
                        select
                        SelectProps={{ native: true }}
                    >
                        {this.props.grupos && this.props.grupos.map(g => <option key={g.id} value={g.id}>
                            {g.nombre}
                        </option>)}
                    </TextField>

                    <FormControlLabel
                        style={{ marginLeft: 15, marginRight: 0, width: 130 }}
                        label={state.habilitado ? "Habilitado" : "Deshabilitado"}
                        control={<Switch
                            checked={state.habilitado}
                            onChange={this.handleCheck}
                            name="habilitado" />}
                    />
                </div>
            </DialogContent>
            <DialogActions>
                {state.error && <Typography style={{ width: "100%" }} color="secondary">{state.error.message}</Typography>}
                <Button
                    fullWidth
                    style={{ ...style.textField, flex: 1 }}
                    disabled={state.loading}
                    variant="outlined"
                    type="button"
                    onClick={this.handleClick} color="primary">
                    {state.loading
                        ? <CircularProgress size={30} color="primary" />
                        : "GUARDAR"}
                </Button>

                {(state.id !== "NUEVO" && (state.eliminable !== false))
                    ? <Button disabled={state.loading} variant="outlined" type="button" onClick={this.handleDelete} color="secondary">
                        {state.loading
                            ? <CircularProgress size={30} color="secondary" />
                            : "ELIMINAR"}
                    </Button>
                    : null}
            </DialogActions>
        </Dialog >)
    }
})

const style = {
    textField: {
        marginTop: 10,
        marginBottom: 10
    }
}