import React from "react";
import { Dialog, DialogContent, CircularProgress, TextField, Button, Typography } from "@material-ui/core";

export default class extends React.PureComponent {
    state = {
        input: "",
        inputLabel: ""
    }
    constructor(props) {
        super(props);
        this.handleInput = this.handleInput.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleClose = this.handleClose.bind(this);

        this.state.input = props.value || "";
        this.state.inputLabel = props.label || "";
    }
    handleInput(event) {
        const { name, value } = event.currentTarget;
        this.setState({ [name]: value });
    }
    handleSubmit(event) {
        event.preventDefault();
        if (this.state.loading) return;
        if (!this.state.input) return this.setState({error : "El texto no puede estar vacío."});
            this.setState({ loading: true });
        if (!this.props.onSubmit) return alert("You Must especify TextPrompt.onSubmit event");
        this.props.onSubmit(this.state.input, error => {
            this.setState({ error, loading: false });
        });
    }
    handleClose(event) {
        if (this.state.loading) return event.preventDefault();
        this.props.onSubmit(undefined, error => {
            this.setState({ error, loading: false });
        });
    }
    render() {
        console.info("TextPrompt rendering");
        const { state } = this;

        return (<Dialog maxWidth="lg" open={true} onClose={this.handleClose} >
            <DialogContent style={{ width: 300 }}>
                <form onSubmit={this.handleSubmit}>
                    <TextField
                        value={state.input}
                        name="input"
                        onChange={this.handleInput}
                        label={state.inputLabel}
                        variant="outlined"
                        fullWidth
                        style={style.textField}
                        autoFocus={true}
                    />
                    {state.error && <Typography color="secondary" variant="body2">{state.error}</Typography>}
                    <Button
                        fullWidth
                        variant="outlined"
                        disabled={state.loading}
                        type="submit"
                        color="primary">
                        {state.loading
                            ? <CircularProgress size={30} color="primary" />
                            : "GUARDAR"}
                    </Button>
                </form>
            </DialogContent>
        </Dialog>)
    }
}

const style = {
    textField: {
        marginTop: 10,
        marginBottom: 10
    }
}