import API from "./API";

export default class {
    static ListenOrderChanges() {
        return API.ListenOnOrderChange(([x]) => {
            let N;
            if (x.estado === 0)
                N = new Notification("NUEVO PEDIDO", {
                    body: x.nombre + " - Orden #" + x.numero,
                    icon: "./favicon.ico"
                });
            if (x.estado === 1)
                N = new Notification("LA ORDEN #" + x.numero + " ESTÁ EN RUTA", {
                    body: x.nombre + " - Orden #" + x.numero,
                    icon: "./favicon.ico"
                });
            if (x.estado === 2)
                N = new Notification("LA ORDEN #" + x.numero + " FUE ENTREGADA", {
                    body: x.nombre + " - Orden #" + x.numero,
                    icon: "./favicon.ico"
                });
            if (x.estado === 3) {
                N = new Notification("LA ORDEN #" + x.numero + " FUE CANCELADA", {
                    body: x.nombre + " - Orden #" + x.numero,
                    icon: "./favicon.ico"
                });
                N = null;
            }
            if (N) N.onclick = () => {
                window.focus();
                window.location.href = "#/orders/" + x.estado + "/" + x.id;
            }
        });

    }
}