import React from "react";
import { Button, Typography, CircularProgress } from "@material-ui/core";
import { AddAPhoto } from "@material-ui/icons";

export default function (props) {
    function onClick() {
        var input = document.getElementById("img-src-input-hidden");
        input.click();
    }
    const IMG_STYLE = { maxHeight: props.height, maxWidth: "100%", cursor: "pointer" };
    if (props.border) IMG_STYLE.border = "1px dotted #949494";

    return (
        <React.Fragment>
            <input type="file" id="img-src-input-hidden" onChange={props.onChange} hidden accept="image/*" />
            {props.loading
                ? (<div style={{ height: props.height, maxWidth: "100%", margin: "auto", display: "table" }}>
                    <div style={{ display: "table-cell", verticalAlign: "middle", height: "100%" }}>
                        <CircularProgress color="primary" size={100} />
                    </div>
                </div>)
                : (props.src
                    ? (<img
                        onClick={onClick}
                        title="Click para cambiar la foto. O arrastra tu imagen."
                        src={props.src}
                        alt={props.name}
                        style={IMG_STYLE}
                    />)
                    : (<div style={{ height: props.height, maxWidth: "100%", margin: "auto", display: "table" }}>
                        <Button
                            variant={props.border ? "outlined" : "text"}
                            type="button"
                            onClick={onClick}
                            style={{ display: "table-cell", verticalAlign: "middle", height: "100%" }}
                        >
                            <AddAPhoto fontSize="large" />
                            <Typography variant="h6">Agrega una foto</Typography>
                        </Button>
                    </div>))
            }
        </React.Fragment >);
}