import React from "react";
import {
    AppBar, Typography, Toolbar, IconButton,
    Card, CardActionArea, CardContent,
    Menu, MenuItem, CircularProgress
} from "@material-ui/core";
import {
    MoreVert, AddCircleOutline,
    ArrowUpward, ArrowDownward
} from "@material-ui/icons";
import Item from "./Item";

export default class extends React.PureComponent {
    state = {
        menuEl: null
    }
    constructor(props) {
        super(props);
        this.handleMenu = this.handleMenu.bind(this);
        this.handleMenuClose = this.handleMenuClose.bind(this);
    }
    handleMenu(event) {
        this.setState({ menuEl: event.target });
    }
    handleMenuClose() {
        this.setState({ menuEl: null });
    }
    render() {
        const { nombre, items, id, loading, loading_action } = this.props;
        console.info("Group " + id + " rendered");
        const { menuEl } = this.state;

        return (<div style={{ width: "calc(100% - 40px)", padding: "10px 20px" }} id={"GROUP-ROOT-" + id}>
            <AppBar position="relative" color="default">
                <Toolbar>
                    {loading_action
                        ? <div style={{ marginRight: 10 }}>
                            <CircularProgress size={20} />
                        </div>
                        : <div style={{ marginRight: 10 }}>
                            <IconButton onClick={this.props.onGroupAction} name="up" value={id}>
                                <ArrowUpward fontSize="small" />
                            </IconButton>
                            <IconButton onClick={this.props.onGroupAction} name="down" value={id}>
                                <ArrowDownward fontSize="small" />
                            </IconButton>
                        </div>}
                    <Typography variant="h6" color="inherit" style={{ flexGrow: 1 }}>
                        {nombre} ({(items && items.length) || 0} items)
                    </Typography>
                    <IconButton disabled={loading} onClick={this.handleMenu}>
                        {loading ? <CircularProgress size={20} color="inherit" /> : <MoreVert />}
                    </IconButton>
                </Toolbar>
            </AppBar>
            <div style={{ width: "100%", overflow: "auto", whiteSpace: "nowrap", scrollBehavior: "smooth" }} id={"GROUP-CONTAINER-" + id}>
                <Card style={{ display: "inline-block", width: 200, margin: 10 }}>
                    <CardActionArea style={{ height: 285, textAlign: "center" }} component="button" value={"NUEVO~" + id} onClick={this.props.onItemClick} >
                        <CardContent>
                            <AddCircleOutline fontSize="large" />
                            <Typography variant="h6">NUEVO</Typography>
                        </CardContent>
                    </CardActionArea>
                </Card>

                {items && items.sort((a, b) => a.orden - b.orden)
                    .map(p => (<Item
                        {...p}
                        onItemClick={this.props.onItemClick}
                        onItemAction={this.props.onItemAction}
                        key={p.id}
                    />))}
            </div>
            <Menu
                anchorEl={menuEl}
                anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                transformOrigin={{ vertical: 'top', horizontal: 'right' }}
                open={Boolean(menuEl)}
                onClose={this.handleMenuClose}
                onClick={this.handleMenuClose}
            >
                <MenuItem value={id} onClick={this.props.onGroupEdit}>Editar</MenuItem>
                <MenuItem value={id} onClick={this.props.onGroupDelete}>Eliminar</MenuItem>
            </Menu >
        </div>)
    }
}