import React from "react";
import { AppBar, Toolbar, Typography, IconButton, Menu, MenuItem } from "@material-ui/core";
import { AccountCircle } from "@material-ui/icons";

export default class extends React.PureComponent {
    state = {
        menuEl: null
    }
    constructor(props) {
        super(props);
        this.handleMenu = this.handleMenu.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleLogOut = this.handleLogOut.bind(this);
    }
    handleMenu(event) {
        this.setState({ menuEl: event.currentTarget });
    }
    handleClose() {
        this.setState({ menuEl: null });
    }
    handleLogOut() {
        var ok = window.confirm("Está seguro de cerrar sesión?");
        if (ok) this.props.onLogOut();
        this.handleClose();
    }
    render() {
        const { title, store, user } = this.props;
        const { menuEl } = this.state;
        const open = Boolean(menuEl);
        return (<AppBar position="fixed" style={{ zIndex: 1201 }}>
            <Toolbar style={{ display: "flex", flexDirection: "row" }}>
                <Typography style={{ flexGrow: 1 }} variant="h4" color="inherit" >{title || ""}</Typography>
                <div style={{ display: "block", textAlign: "right" }}>
                    <Typography color="inherit" variant="button">{(store.titulo || "").toUpperCase()}</Typography>
                    <Typography color="inherit" variant="caption">{user}</Typography>
                </div>
                <div>
                    <IconButton
                        title={store.titulo}
                        aria-owns={open ? 'menu-appbar' : undefined}
                        aria-haspopup="true"
                        onClick={this.handleMenu}
                        color="inherit"
                    >
                        <AccountCircle />
                    </IconButton>
                    <Menu
                        id="menu-appbar"
                        anchorEl={menuEl}
                        anchorOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        transformOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        open={open}
                        onClose={this.handleClose}
                    >
                        <MenuItem style={{ minWidth: 150 }} onClick={this.handleLogOut}>Salir</MenuItem>
                    </Menu>
                </div>
            </Toolbar>
        </AppBar>)
    }
};