import React from "react";
import { withScriptjs, GoogleMap, withGoogleMap, Polygon } from "react-google-maps";
import { MarkerWithLabel } from "react-google-maps/lib/components/addons/MarkerWithLabel";
import { Typography, Card, CardContent } from "@material-ui/core";
import DrawingManager from "react-google-maps/lib/components/drawing/DrawingManager";

export default function (props) {
    const { API_KEY, ...mapProps } = props;
    return (<Map
        googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${API_KEY || ""}&v=3.exp&libraries=geometry,drawing,places`}
        containerElement={<div style={{ height: "100%" }} />}
        loadingElement={<div style={{ height: `100%` }} />}
        mapElement={<div style={{ height: `100%` }} />}
        defaultZoom={15}
        {...mapProps}
    />)
}

const Map = withScriptjs(withGoogleMap(props => {
    const google = window.google;
    const { marker, polygons, enableDrawingManager
        , onPolygonComplete, onMapMounted, onPolygonClick, onPolygonMouseUp,
        ...mapProps } = props;
    return (<GoogleMap
        {...mapProps}
        options={{
            disableDefaultUI: true,
            zoomControl: true,
            clickableIcons: false
        }}
        ref={onMapMounted}
    >
        {marker && <MarkerWithLabel
            labelAnchor={new google.maps.Point(0, 0)}
            position={marker.position}
        >
            <Card elevation={3} color="secondary" style={{ margin: 5 }}>
                <CardContent style={{ maxWidth: 200, padding: 5 }}>
                    <Typography variant="button">{marker.label}</Typography>
                </CardContent>
            </Card>
        </MarkerWithLabel>}

        {enableDrawingManager &&
            <DrawingManager
                onPolygonComplete={onPolygonComplete}
                defaultDrawingMode={google.maps.drawing.OverlayType.POLYGON}
                defaultOptions={{
                    drawingControl: true,
                    drawingControlOptions: {
                        position: google.maps.ControlPosition.TOP_CENTER,
                        drawingModes: ["polygon"]
                    }
                }}
                polygonOptions={{
                    strokeColor: '#007bff',
                    strokeOpacity: 0.8,
                    strokeWeight: 2,
                    fillColor: '#007bff',
                    fillOpacity: 0.35
                }}
            />}

        {polygons && polygons.map((p, index) => (
            <Polygon
                key={index}
                path={p.path}
                editable={p.editable}
                onClick={onPolygonClick}
                onMouseUp={function (event) { onPolygonMouseUp && onPolygonMouseUp(this, event, index) }}
                options={{
                    strokeColor: '#007bff',
                    strokeOpacity: 0.8,
                    strokeWeight: 2,
                    fillColor: '#007bff',
                    fillOpacity: 0.35
                }}
            />
        ))}
    </GoogleMap >)
}));