import React from "react";
import Group from "./Group";
import API from "../../Services/API";
import { Toolbar, AppBar, Typography, CardActionArea, CircularProgress } from "@material-ui/core";
import { AddCircleOutline } from "@material-ui/icons";
import { Route } from "react-router-dom";
import ProductDialog from "./ProductDialog";
import TextPrompt from "../TextPrompt";

export default class extends React.Component {
    state = {
        grupos: [],
        loading: true,
        // Habilita/deshabilita TextPrompt del grupo
        currentGroup: null
    }
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepID);

        this.deleteGroup = this.deleteGroup.bind(this);
        this.editGroup = this.editGroup.bind(this);
        this.editGroupEnding = this.editGroupEnding.bind(this);
        this.updateGrupo = this.updateGrupo.bind(this);
        this.handleGroupAction = this.handleGroupAction.bind(this);

        this.handleItemClick = this.handleItemClick.bind(this);
        this.handleItemAction = this.handleItemAction.bind(this);
        this.editItemEnd = this.editItemEnd.bind(this);
        this.updateProduct = this.updateProduct.bind(this);
    }
    componentDidMount() {
        API.GetGroups().then(rawGroups => {
            var grupos = rawGroups.sort((a, b) => a.orden - b.orden)
                .map(x => {
                    if (!x.items) x.items = [];
                    x.items = x.items.sort((a, b) => a.orden - b.orden);
                    return x;
                });
            this.setState({ grupos, loading: false });
        }).catch(error => {
            this.setState({ loading: false, error });
        });
    }

    handleItemAction(event) {
        const id = +event.currentTarget.value;
        const action = event.currentTarget.name;
        this.updateProduct("loading", { id, loading: action });
        API.RequestActionProduct({ id, action })
            .then(items => {
                for (let i = 0; i < items.length; i++) {
                    const item = items[i];
                    this.updateProduct(null, { ...item, loading: null });
                }
            }).catch(ex => {
                alert(ex.message);
                this.updateProduct("loading", { id, loading: null });
            });
    }
    handleItemClick(event) {
        const id = event.currentTarget.value;
        if (id.indexOf("NUEVO") >= 0) {
            this.updateProduct("loading", { id: +id, loading: true })
        }
        this.props.history.push(this.props.match.path + "/" + id);
    }
    editItemEnd(product) {
        if (product) {
            this.updateProduct(null, product);
        }
        this.props.history.push(this.props.match.path);
    }
    updateProduct(prop_name, product) {
        this.setState(prev => {
            product.idGrupo = +product.idGrupo;
            const state = { ...prev };
            state.grupos = prev.grupos;
            var grupo = state.grupos.find(x => x.items && x.items.find(i => i.id === product.id));
            var grupo_a_actualizar;

            if (product.delete) {
                if (grupo) {
                    let index = grupo.items.indexOf(
                        grupo.items.find(x => x.id === product.id)
                    );
                    grupo.items.splice(index, 1);
                    grupo.items = [...grupo.items].sort((a, b) => a.orden - b.orden);
                }
                return state;
            }

            if (grupo) {
                let item = grupo.items.find(x => x.id === product.id);
                let index = grupo.items.indexOf(item);
                if (product.idGrupo && product.idGrupo !== grupo.id) {
                    // CAMBIO DE GRUPO
                    grupo.items.splice(index, 1);
                    grupo.items = [...grupo.items].sort((a, b) => a.orden - b.orden);
                    grupo_a_actualizar = state.grupos.find(x => x.id === product.idGrupo);
                    if (grupo_a_actualizar) {
                        grupo_a_actualizar.items.push(product);
                        this.scrollGroupToItem(grupo_a_actualizar.id, product.id);
                    }
                } else {
                    grupo_a_actualizar = grupo;
                    if (prop_name) {
                        item[prop_name] = product[prop_name];
                    } else {
                        var new_element = { ...item };
                        for (const key in product) {
                            if (product.hasOwnProperty(key)) {
                                new_element[key] = product[key];
                            }
                        }
                        grupo.items[index] = new_element;
                    }
                }
            } else {
                // ITEM NO EXISTE
                grupo_a_actualizar = state.grupos.find(x => x.id === product.idGrupo);
                if (grupo_a_actualizar) {
                    grupo_a_actualizar.items.push(product);
                    this.scrollGroupToItem(grupo_a_actualizar.id, product.id);
                }
            }
            if (grupo_a_actualizar) {
                grupo_a_actualizar.items = [...grupo_a_actualizar.items].sort((a, b) => a.orden - b.orden);
            }
            return state;
        });
    }

    deleteGroup(event) {
        const id = +event.currentTarget.value;
        if (!id) return;
        var confirmed = window.confirm("Esta seguro de borrar este registro?");
        if (!confirmed) return;
        this.updateGrupo("loading", { id, loading: true });
        API.DeleteGroup({ id })
            .then(x => {
                this.setState(prev => {
                    const state = { ...prev };
                    state.grupos = prev.grupos;
                    var exists = state.grupos.find(x => x.id === id);
                    if (exists) state.grupos.splice(state.grupos.indexOf(exists), 1);
                    return state.grupos;
                });
            }).catch(ex => {
                alert(ex.message);
                this.updateGrupo("loading", { id, loading: false });
            });
    }
    editGroup(event) {
        const { value } = event.currentTarget;
        if (value === "NUEVO")
            return this.setState({ currentGroup: {} });
        var group = this.state.grupos.find(x => x.id === (+value));
        this.setState({
            currentGroup: {
                id: group.id,
                value: group.nombre
            }
        });
    }
    handleGroupAction(event) {
        const id = +event.currentTarget.value;
        const action = event.currentTarget.name;
        this.updateGrupo("loading_action", { id, loading_action: true });

        API.RequestActionGroup({ id, action })
            .then(results => {
                results.forEach(g => {
                    this.updateGrupo(null, { ...g, loading_action: null });
                });
                this.scrollToGroupId(id);
            }).catch(ex => {
                this.updateGrupo("loading_action", { id, loading_action: null });
                alert(ex.message);
            })
    }
    editGroupEnding(nombre, onError) {
        if (!nombre) return this.setState({ currentGroup: null });
        const grupo = this.state.currentGroup;
        if (grupo.value === nombre) return this.setState({ currentGroup: null });

        var exists = this.state.grupos.find(a =>
            a.id !== grupo.id &&
            a.nombre.toUpperCase() === nombre.toUpperCase()
        );
        if (exists) return onError("Este nombre ya existe");
        API.SaveGroup({ id: grupo.id, nombre })
            .then(this.updateGrupo.bind(this, "nombre"))
            .catch(err => onError(err.message));
    }
    scrollToGroupId(ID, then) {
        setTimeout(() => {
            window.requestAnimationFrame(() => {
                var div = document.getElementById("product-div");
                var groupDIV = document.getElementById("GROUP-ROOT-" + ID);
                if (!groupDIV) return;
                var scrollTop = groupDIV.offsetTop - 70;
                div.scrollTop = scrollTop;
                if (then) then();
            })
        });
    }
    scrollGroupToItem(idGrupo, idProducto) {
        this.scrollToGroupId(idGrupo, () => {
            setTimeout(() => {
                window.requestAnimationFrame(() => {
                    var div = document.getElementById("GROUP-CONTAINER-" + idGrupo);
                    var item = document.getElementById("ITEM-CONTAINER-" + idProducto);
                    if (!item) return;
                    div.scrollLeft = item.offsetLeft;
                });
            }, 300);
        })
    }
    updateGrupo(changed_prop, group) {
        this.setState(prev => {
            const state = { ...prev };
            state.grupos = prev.grupos;
            var exists = state.grupos.find(x => x.id === group.id);
            if (exists) {
                if (changed_prop) {
                    var props = changed_prop.split(",");
                    props.forEach(changed_prop => {
                        exists[changed_prop] = group[changed_prop];
                    });
                } else {
                    group.items = exists.items;
                    state.grupos[state.grupos.indexOf(exists)] = { ...exists, ...group };
                }
            } else {
                state.grupos.push(group);
                this.scrollToGroupId(group.id);
            }
            state.currentGroup = null;
            return state;
        });
    }

    render() {

        console.info("Product view rendered");

        const {
            grupos, loading, error,
            currentGroup
        } = this.state;


        if (loading) {
            return (<div style={{ width: "100%", textAlign: "center", paddingTop: 100 }}>
                <CircularProgress size={100} />
                <Typography variant="h4">CARGANDO...</Typography>
            </div>)
        }


        if (error) {
            return (<div style={{ textAlign: "center", paddingTop: 200 }}>
                <Typography variant="subtitle1"> Ocurrio un error</Typography>
                <Typography variant="body1">{error.message}</Typography>
                <Typography variant="caption">{error.code} {error.status}</Typography>
            </div>)
        }


        return (<div style={{ height: "100%", overflow: "auto", scrollBehavior: "smooth" }} id="product-div">

            <div style={{ width: "calc(100% - 40px)", padding: 20 }} >
                <AppBar position="relative" color="default">
                    <CardActionArea onClick={this.editGroup} value="NUEVO">
                        <Toolbar>
                            <AddCircleOutline style={{ marginRight: 10 }} />
                            <Typography variant="h6" color="inherit" style={{ flexGrow: 1 }}>
                                NUEVO GRUPO
                        </Typography>
                        </Toolbar>
                    </CardActionArea>
                </AppBar>
            </div>


            {grupos && grupos.length
                ? grupos.sort((a, b) => a.orden - b.orden).map(x => (<Group
                    onGroupAction={this.handleGroupAction}
                    onGroupEdit={this.editGroup}
                    onGroupDelete={this.deleteGroup}

                    onItemAction={this.handleItemAction}
                    onItemClick={this.handleItemClick}
                    key={x.id}
                    {...x}
                />))
                : null}

            <Route path={this.props.match.path + "/:info"}
                render={props => <ProductDialog
                    porcentajeIVA={12}
                    grupos={grupos.map(d => ({ id: d.id, nombre: d.nombre }))}
                    onClose={this.editItemEnd}
                    {...props}
                />}
            />

            {currentGroup && <TextPrompt label="Nombre del Grupo" {...currentGroup} onSubmit={this.editGroupEnding} />}

        </div>);
    }
}