import React from "react";
import {
    Dialog, DialogContent,
    Select, FormControlLabel,
    Checkbox, DialogActions, Button,
    Typography,
    DialogTitle
} from "@material-ui/core";

export default class extends React.PureComponent {
    state = {
        dias: [],
        desde: [0, 0],
        hasta: [0, 0]
    }
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleDays = this.handleDays.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleClose = this.handleClose.bind(this);
        if (props.data && props.data !== "NUEVO") {
            this.state = { ...props.data };
        }
    }
    handleChange(event) {
        const { value, name } = event.currentTarget;
        if (name.indexOf(".") >= 0) {
            var parts = name.split(".");
            this.setState(prev => {
                const state = JSON.parse(JSON.stringify(prev));
                state[parts[0]][parts[1]] = value;
                return state;
            });
        } else {
            this.setState({ [name]: value });
        }
    }
    handleDays(event) {
        const { value } = event.currentTarget;
        this.setState(prev => {
            const state = JSON.parse(JSON.stringify(prev));
            state.dias = [...state.dias];
            var INDEX = state.dias.indexOf(+value);
            if (INDEX >= 0) {
                state.dias.splice(INDEX, 1);
            } else {
                state.dias.push(+value);
            }
            return state;
        });
    }
    handleClick() {
        const { loading, dias, desde, hasta } = this.state;
        if (loading) return;
        if (!dias.length) return alert("Selecciona al menos un dia");
        if (desde[0] === hasta[0] && desde[1] === hasta[1]) return alert("Las horas de inicio y fin no pueden ser iguales");
        this.setState({ loading: true });
        this.props.guardar(this.state)
            .catch(ex => {
                alert(ex.message);
                this.setState({ loading: false });
            });
    }
    handleClose() {
        if (this.state.loading) return;
        this.props.onClose && this.props.onClose();
    }
    render() {
        const { desde, hasta, dias, loading } = this.state;
        return (<Dialog open={true} onClose={this.handleClose} maxWidth="lg">
            <DialogTitle>HORARIO</DialogTitle>
            <DialogContent>
                <div style={{ display: "table" }}>
                    <div style={{ display: "table-cell", width: 150 }}>
                        <Typography variant="subtitle1">INICIO</Typography>
                        <div>
                            <TimeSelect label="Horas" length={23} name="desde.0" value={desde[0]} onChange={this.handleChange} />
                            <Typography style={{ display: "inline", width: "auto", marginLeft: 5, marginRight: 5 }} variant="subtitle1" >:</Typography>
                            <TimeSelect label="Minutos" items={[0, 15, 30, 45]} name="desde.1" value={desde[1]} onChange={this.handleChange} />
                        </div>
                    </div>

                    <div style={{ display: "table-cell", width: 150 }}>
                        <Typography variant="subtitle1">FIN</Typography>
                        <div>
                            <TimeSelect label="Horas" length={23} name="hasta.0" value={hasta[0]} onChange={this.handleChange} />
                            <Typography style={{ display: "inline", width: "auto", marginLeft: 5, marginRight: 5 }} variant="subtitle1" >:</Typography>
                            <TimeSelect label="Minutos" items={[0, 15, 30, 45]} name="hasta.1" value={hasta[1]} onChange={this.handleChange} />
                        </div>
                    </div>
                    <div style={{ display: "table-cell" }}>
                        <Typography variant="subtitle1">SELECCIONA LOS DIAS</Typography>
                        <DaysSelect value={dias} onChange={this.handleDays} />
                    </div>
                </div>
                <DialogActions >
                    <Button disabled={loading} style={{ flex: 1 }} color="primary" variant="outlined" onClick={this.handleClick}>
                        GUARDAR
                        </Button>
                </DialogActions>
            </DialogContent>
        </Dialog>)
    }
}
function TimeSelect(props) {
    const { length, items, ...selectProps } = props;
    var options = [];
    if (items) {
        options = items.map(i => ({
            value: i,
            label: i.toString().length === 1 ? `0${i}` : i
        }))
    } else {
        for (let i = 0; i <= length; i++) {
            options.push({
                value: i,
                label: i.toString().length === 1 ? `0${i}` : i
            });
        }
    }
    return (<Select native={true} {...selectProps}>
        {options.map(o => <option key={o.value} value={o.value}>{o.label}</option>)}
    </Select>);
}
function DaysSelect(props) {
    const value = props.value || [];
    return (<div style={props.style}>
        <FormControlLabel
            label="Lun"
            control={<Checkbox value={"1"} checked={value.indexOf(1) >= 0} onChange={props.onChange} />}
        />
        <FormControlLabel
            label="Mar"
            control={<Checkbox value={"2"} checked={value.indexOf(2) >= 0} onChange={props.onChange} />}
        />
        <FormControlLabel
            label="Mie"
            control={<Checkbox value={"3"} checked={value.indexOf(3) >= 0} onChange={props.onChange} />}
        />
        <FormControlLabel
            label="Jue"
            control={<Checkbox value={"4"} checked={value.indexOf(4) >= 0} onChange={props.onChange} />}
        />
        <FormControlLabel
            label="Vie"
            control={<Checkbox value={"5"} checked={value.indexOf(5) >= 0} onChange={props.onChange} />}
        />
        <FormControlLabel
            label="Sab"
            control={<Checkbox value={"6"} checked={value.indexOf(6) >= 0} onChange={props.onChange} />}
        />
        <FormControlLabel
            label="Dom"
            control={<Checkbox value={"0"} checked={value.indexOf(0) >= 0} onChange={props.onChange} />}
        />
    </div>)
}