import React from "react";
import { Typography, CircularProgress, Tabs, Tab, FormControlLabel, Checkbox } from "@material-ui/core";
import Geo from "../../Services/Geo";
import Map from "../Map";
import UpdateableInput from "./UpdateableInput";
import API from "../../Services/API";
import { ReportProblem, Layers, LocationOn } from "@material-ui/icons";

export default class extends React.PureComponent {
    state = {
        position: null,
        baseTransporte: 0,
        kmBaseTransporte: 0,
        recargoPorKm: 0,
        google_maps_key: "",
        cobrarTransporte: false,

        coberturas: [],
        // esto es el modo de edicion del mapa (0=UBICACION o 1=COBERTURA)
        editMode: 0,
        loading: true,
        defaultCenter: null,
        feedback: {}
    }
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepID);
        this.handleInput = this.handleInput.bind(this);
        this.handleUpdateProp = this.handleUpdateProp.bind(this);
        this.handleCheck = this.handleCheck.bind(this);

        this.handleMapMounted = this.handleMapMounted.bind(this);
        this.handleNewPolygon = this.handleNewPolygon.bind(this);
        this.handlePolygonMouseUp = this.handlePolygonMouseUp.bind(this);
        this.handleEditMode = this.handleEditMode.bind(this);
        this.handleMapClick = this.handleMapClick.bind(this);


        const { configuracion } = props;
        if (configuracion) {
            for (const k in this.state) {
                if (configuracion.hasOwnProperty(k)) {
                    this.state[k] = configuracion[k];
                }
            }
        }
        if (this.state.position) this.state.defaultCenter = this.state.position;
        if (this.state.defaultCenter) {
            this.state.loading = null;
            this.state.editMode = 1;
        }
    }

    handleUpdateProp(name, value, avoidCatch) {
        if (this.state.feedback[name] === "loading") return;
        this.updateFeedback(name, "loading");
        var promise = API.setConfigInfo({
            [name]: value === undefined ? this.state[name] : value
        }).then(() => this.updateFeedback(name, null))
        if (avoidCatch) return promise;
        return promise.catch(err => this.updateFeedback(name, err.message));
    }
    updateFeedback(name, feedback) {
        this.setState(prev => {
            const state = { ...prev };
            state.feedback = { ...state.feedback, [name]: feedback };
            return state;
        });
    }

    componentDidMount() {
        Geo.currentPosition()
            .then(defaultCenter => this.setState({ defaultCenter, loading: null }))
            .catch(error => this.setState({ error, loading: "ERROR" }));
    }
    handleInput(event) {
        const { name, value } = event.currentTarget;
        this.setState({ [name]: value });
    }
    handleCheck(event) {
        event.preventDefault();
        const { name, checked } = event.currentTarget;
        var promise = this.handleUpdateProp(name, checked, true)
        if (promise) promise.then(x => {
            this.setState({ [name]: checked })
        }).catch(ex => {
            alert(ex.message);
            this.updateFeedback(name, null);
        });
    }
    handleNewPolygon(Polygon) {
        var path = [];
        var vertices = Polygon.getPath();
        for (var i = 0; i < vertices.getLength(); i++) {
            path.push(vertices.getAt(i).toJSON());
        }
        const coberturas = [...this.state.coberturas, { path }];
        var promise = this.handleUpdateProp("coberturas", coberturas, true);
        if (promise) {
            promise
                .then(() => {
                    this.setState({ coberturas });
                    Polygon.setMap(null);
                }).catch(ex => {
                    this.updateFeedback("coberturas", ex.message);
                    Polygon.setMap(null);
                });
        }
    }
    handleMapClick(event) {
        const position = event.latLng.toJSON();
        if (this.state.editMode === 0) {
            var promise = this.handleUpdateProp("position", position, true);
            if (promise) {
                promise.then(() => this.setState({ position }))
                    .catch(ex => this.updateFeedback("position", ex.message));
                this.setState({ position });
            }
        }
    }
    handlePolygonMouseUp(Polygon, event, index) {
        // en caso de que se aplaste el mouse sobre el Poligono, pero
        // sea para situar la Ubicación. 
        if (this.state.editMode !== 1) return;
        var path = [];
        var vertices = Polygon.getPath();
        for (var i = 0; i < vertices.getLength(); i++) {
            path.push(vertices.getAt(i).toJSON());
        }
        const coberturas = JSON.parse(JSON.stringify(this.state.coberturas));
        var has_changed = false;
        var previous = coberturas[index];
        // esto es solo de seguridad
        if (previous) {
            for (let i = 0; i < path.length; i++) {
                const coord = path[i];
                var prev_coord = previous.path[i];
                if (!prev_coord) {
                    has_changed = true;
                    break;
                }
                var isDifferent = (prev_coord.lat !== coord.lat || prev_coord.lng !== coord.lng);
                if (isDifferent) {
                    has_changed = true;
                    break;
                }
            }
        } else {
            has_changed = true;
        }
        if (!has_changed) return;
        coberturas[index] = { path };
        var promise = this.handleUpdateProp("coberturas", coberturas, true);
        if (promise) {
            if (!this.LAST_COBERTURAS) {
                this.LAST_COBERTURAS = JSON.parse(JSON.stringify(this.state.coberturas));
            }
            promise
                .then(() => {
                    //this.setState({ coberturas })
                    this.LAST_COBERTURAS = coberturas;
                }).catch(ex => {
                    this.updateFeedback("coberturas", ex.message);
                    this.setState(
                        { coberturas: [] }, () =>
                        this.setState({ coberturas: this.LAST_COBERTURAS })
                    )
                });
        }
    }
    handleMapMounted(map) {
        this.mapRef = map;
        this._fitBoundsToPolygon();
    }
    handleEditMode(_, value) {
        this.setState({ editMode: value });
    }
    // pone el recangulo de visualizacion, en el lugar correcto
    // para abarcar todo/s el/los poligono/s
    _fitBoundsToPolygon() {
        if (!this.mapRef) return;
        if (!this.state.coberturas.length) return;
        const google = window.google;
        const bounds = new google.maps.LatLngBounds();
        for (let i = 0; i < this.state.coberturas.length; i++) {
            const c = this.state.coberturas[i];
            c.path.forEach(coords => bounds.extend(coords));
        }
        var current_bounds = this.mapRef.getBounds();
        if (current_bounds) bounds.union(current_bounds);
        this.mapRef.fitBounds(bounds);
    }

    render() {
        const { loading, error, editMode,
            baseTransporte, kmBaseTransporte, recargoPorKm, feedback, cobrarTransporte,
            defaultCenter, position, google_maps_key,
            coberturas
        } = this.state;
        var hay_coberturas = coberturas && coberturas.length;

        var MAP_OPTIONS = {
            defaultCenter: defaultCenter,
            API_KEY: google_maps_key,
            enableDrawingManager: !hay_coberturas && editMode === 1
        }
        if (position)
            MAP_OPTIONS.marker = {
                position: position,
                label: "Tu Tienda"
            };
        if (coberturas && coberturas.length)
            MAP_OPTIONS.polygons = coberturas.map(x => ({ ...x, editable: editMode === 1 }));

        if (loading === "ERROR")
            return (<div style={{ width: "100%", paddingTop: 100, textAlign: "center" }}>
                <CircularProgress size={100} />
                <Typography variant="h4">Ocurrió un error</Typography>
                <Typography variant="subtitle1">{error.message}</Typography>
                <Typography variant="body1">{error.code} {error.status}</Typography>
            </div>);

        if (loading)
            return (<div style={{ width: "100%", paddingTop: 100, textAlign: "center" }}>
                <CircularProgress size={100} />
                <Typography variant="h4">CARGANDO EL MAPA...</Typography>
            </div>);

        return (<div style={{ display: "flex", flexDirection: "row", height: "calc(100% - 100px)" }}>
            <div style={{ flex: 1, padding: 10 }}>
                <div style={{ display: "flex", flexDirection: "row" }}>
                    <Typography style={{ marginLeft: 10, flex: 1, paddingTop: 5 }} variant="h6">Ubicación de la Tienda</Typography>
                    <Tabs onChange={this.handleEditMode} value={editMode}>
                        <Tab
                            title={feedback["position"]}
                            label={(position ? "Cambiar" : "Agregar") + " Ubicación"}
                            icon={(
                                feedback["position"] && (feedback["position"] === "loading"
                                    ? < CircularProgress size={20} color="primary" />
                                    : <ReportProblem color="secondary" />)
                            ) || <LocationOn />}
                        />
                        <Tab
                            title={feedback["coberturas"]}
                            label={(hay_coberturas ? "Cambiar" : "Agregar") + " Cobertura"}
                            icon={(
                                feedback["coberturas"] && (feedback["coberturas"] === "loading"
                                    ? < CircularProgress size={20} color="primary" />
                                    : <ReportProblem color="secondary" />)
                            ) || <Layers />}
                            disabled={!position}
                        />
                    </Tabs>
                </div>

                {defaultCenter && <Map
                    onMapMounted={this.handleMapMounted}
                    onClick={this.handleMapClick}
                    defaultZoom={12}
                    onPolygonComplete={this.handleNewPolygon}
                    onPolygonClick={this.handleMapClick}
                    onPolygonMouseUp={this.handlePolygonMouseUp}
                    {...MAP_OPTIONS}
                />}
            </div>

            <div style={{ width: 300, padding: 10, textAlign: "center" }}>
                <Typography style={{ margin: 10 }} variant="h6">Tarifa de Transporte</Typography>

                <FormControlLabel
                    label="Cobrar Servicio de Transporte"
                    control={feedback["cobrarTransporte"]
                        ? <CircularProgress size={20} color="primary" style={{ margin: 12 }} />
                        : <Checkbox checked={cobrarTransporte} onChange={this.handleCheck} name="cobrarTransporte" />}
                />
                {cobrarTransporte && (<React.Fragment>
                    <UpdateableInput
                        value={baseTransporte}
                        onChange={this.handleInput}
                        onUpdate={this.handleUpdateProp}
                        label="Tarifa base"
                        name="baseTransporte"
                        type="number"
                        feedback={feedback}
                        inputProps={{
                            step: "0.01"
                        }}
                    />
                    <UpdateableInput
                        value={kmBaseTransporte}
                        onChange={this.handleInput}
                        onUpdate={this.handleUpdateProp}
                        label="Kms que cubre la tarifa base"
                        name="kmBaseTransporte"
                        type="number"
                        feedback={feedback}
                        inputProps={{
                            step: "0.01"
                        }}
                    />
                    <UpdateableInput
                        value={recargoPorKm}
                        onChange={this.handleInput}
                        onUpdate={this.handleUpdateProp}
                        label="Recargo por km adicional"
                        name="recargoPorKm"
                        type="number"
                        feedback={feedback}
                        inputProps={{
                            step: "0.01"
                        }}
                    />
                </React.Fragment>)}

            </div>
        </div>)
    }
}