import React from "react";
import { CircularProgress, Typography, Button, Tabs, Tab } from "@material-ui/core";
import { Error } from "@material-ui/icons";
import { withGoogleMap, GoogleMap, withScriptjs } from "react-google-maps";
import { MarkerWithLabel } from "react-google-maps/lib/components/addons/MarkerWithLabel";
import API from "../../Services/API";
import OrdersTable from "./OrdersTable";
import { Route, Link, Switch, Redirect } from "react-router-dom";

export default class extends React.Component {
    state = {
        now: new Date().valueOf(),
        data: [],
        loading: true,
        goToCenter: null,
        navigationValue: 0
    }
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepID);
        this.handleRightClick = this.handleRightClick.bind(this);
        this.handleSelect = this.handleSelect.bind(this);
        this.handleCurrentLocation = this.handleCurrentLocation.bind(this);
        this.handleNavigation = this.handleNavigation.bind(this);
        this.handleChangeState = this.handleChangeState.bind(this);
        this.__updateRecords = this.__updateRecords.bind(this);
    }
    componentDidMount() {
        // por segundo hasta llegar al minuto
        /*this.STOP_TIMER = setMinuteTimer(() => {
            this.setState({ now: new Date().valueOf() })
        });*/
        API.GetOrders()
            .then(data => {
                this.setState({ data, loading: false });
                this.REMOVE_LISTENER = API.ListenOnOrderChange(this.__updateRecords);
            }).catch(error => this.setState({ error, loading: false }));
    }
    componentWillUnmount() {
        this.STOP_TIMER && this.STOP_TIMER();
        this.REMOVE_LISTENER && this.REMOVE_LISTENER();
    }
    handleRightClick(item) {

    }
    handleSelect(event) {
        event.stopPropagation();
        const { value, checked } = event.target;
        this.setState(function (prev) {
            const state = { ...prev };
            state.data = prev.data;
            const item = state.data.find(x => x.id === +value);
            if (!item) return state;
            item.selected = checked;
            if (item.selected) {
                state.goToCenter = { lat: +item.lat, lng: +item.lng };
            }
            return state;
        });
    }
    handleCurrentLocation() {
        this.setState({
            goToCenter: this.props.store.position
        });
    }
    handleNavigation(event, navigationValue) {
        this.setState({ navigationValue });
    }
    handleChangeState() {
        const { data, navigationValue } = this.state;
        const selections = data.filter(x => x.selected && x.estado === navigationValue);
        if (!selections.length) return alert("Seleccione al menos un registro");
        const IDS = selections.map(x => x.id);
        this.setState({ loading: true });
        if (navigationValue === 0) {
            return API.OrdersOnRoute(IDS)
                .then(this.__updateRecords)
                .catch(error => this.setState({ error, loading: false }));
        }
        if (navigationValue === 1) {
            return API.OrdersDelivered(IDS)
                .then(this.__updateRecords)
                .catch(error => this.setState({ error, loading: false }));
        }
        this.setState({ loading: false });
        alert("No se encontro el navigationValue=" + navigationValue);
    }
    __updateRecords(results, addIfNotExists) {
        this.setState(prev => {
            const state = { ...prev, loading: false };
            state.data = prev.data;
            results.forEach(res => {
                const item = state.data.find(x => x.id === res.id);
                if (item) {
                    return state.data[state.data.indexOf(item)] = { ...item, selected: false, ...res };
                }
                if (!item && addIfNotExists) state.data.push(res);
            });
            return state;
        });
    }
    render() {
        const { now, loading, error, data, goToCenter, navigationValue } = this.state;

        if (loading) {
            return (<div style={{ width: "100%", textAlign: "center", margin: "auto", paddingTop: 100 }}>
                <CircularProgress size={70} />
                <Typography variant="h4" >CARGANDO...</Typography>
            </div>);
        }
        if (error) {
            return (<div style={{ width: "100%", textAlign: "center", margin: "auto", paddingTop: 100 }}>
                <Error size={70} />
                <Typography variant="h4">Ocurrió un error</Typography>
                <Typography variant="subtitle1">{error.message}</Typography>
                <Typography variant="body1">{error.status} | {error.statusText} | {error.code}</Typography>
            </div>)
        }

        const G_M_K = (this.props.store && this.props.store.G_M_K) || "";
        const selections = data.filter(x => x.selected && x.estado === navigationValue);

        return (<div style={{ display: "flex", flexDirection: "row", height: "100%" }}>
            <div style={{ flex: 1, overflow: "auto", height: "100%" }}>
                <Tabs textColor="primary" indicatorColor="primary" style={{ width: "100%" }} color="primary" value={navigationValue} onChange={this.handleNavigation}>
                    <Tab style={{ width: "32%" }} label="Pendientes" component={Link} to={this.props.match.path + "/0"} />
                    <Tab style={{ width: "32%" }} label="En Ruta" component={Link} to={this.props.match.path + "/1"} />
                    <Tab style={{ width: "32%" }} label="Entregado" component={Link} to={this.props.match.path + "/2"} />
                </Tabs>
                <Switch>
                    <Route path={this.props.match.path + "/0"} render={props =>
                        (<OrdersTable
                            onMounted={this.handleNavigation}
                            {...props}
                            data={data}
                            timeCol={"fecha"}
                            selections={selections}
                            onSelect={this.handleSelect}
                            now={now}
                            estado={0}
                        />)} />
                    <Route path={this.props.match.path + "/1"} render={props =>
                        (<OrdersTable
                            onMounted={this.handleNavigation}
                            {...props}
                            data={data}
                            timeCol={"onRouteAt"}
                            selections={selections}
                            onSelect={this.handleSelect}
                            estado={1}
                        />)} />
                    <Route path={this.props.match.path + "/2"} render={props =>
                        (<OrdersTable
                            onMounted={this.handleNavigation}
                            {...props}
                            data={data}
                            timeCol={"deliveredAt"}
                            selections={selections}
                            onSelect={this.handleSelect}
                            estado={2}
                        />)} />
                    <Redirect path={this.props.match.path} to={this.props.match.path + "/0"} />
                </Switch>
                {navigationValue === 2
                    ? null
                    : (<div style={{ display: "flex", flexDirection: "row", paddingLeft: 10, paddingRight: 10 }}>
                        <Button disabled={!selections.length} style={{ flex: 1 }} variant="outlined" color="primary" onClick={this.handleChangeState}>{submitText(navigationValue)}</Button>
                        <Button
                            variant="outlined"
                            style={{ marginLeft: 10 }}
                            color="secondary"
                            disabled={!selections.length}
                            target="_blank"
                            href={ruta(this.props.store.position, selections || [])}
                            component="a"
                        >Ruta</Button>
                    </div>)}
            </div>
            <div style={{ flex: 1, overflow: "auto", height: "100%", display: "flex", flexDirection: "column" }}>
                <div style={{ flex: 1 }}>
                    <Map
                        googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${G_M_K || ""}&v=3.exp&libraries=geometry,drawing,places`}
                        containerElement={<div style={{ height: "100%" }} />}
                        loadingElement={<div style={{ height: `100%` }} />}
                        mapElement={<div style={{ height: `100%` }} />}
                        selections={selections}
                        store={this.props.store}
                        center={goToCenter || this.props.store.position}
                    >
                    </Map>
                </div>
            </div>
        </div>)
    }
}

function ruta(currentLocation, locations) {
    const rutas = [currentLocation];
    for (let i = 0; i < locations.length; i++) {
        rutas.push(locations[i]);
    }
    if (rutas.length === 1) return "";
    return "https://www.google.com/maps/dir/ " +
        rutas.map(x => [x.lat, x.lng].join(",")).join("/");
}

