const Map = withScriptjs(withGoogleMap(props => {
    return (<GoogleMap
        {...props}
        defaultCenter={props.store.position}
        defaultZoom={15}
        options={{
            disableDefaultUI: true,
            zoomControl: true,
            clickableIcons: false
        }}
    >
        <MarkerWithLabel
            labelAnchor={new window.google.maps.Point(0, 0)}
            position={props.store.position}
            options={{
                icon: "./store.png"
            }}
            labelStyle={{ background: "#fff", border: "1px solid #29b6f6", padding: "2px 10px" }}
        >
            <Typography variant="body1" color="inherit">Tu Tienda</Typography>
        </MarkerWithLabel>
        {props.selections && props.selections.map(x => (<MarkerWithLabel
            key={x.id}
            labelAnchor={new window.google.maps.Point(0, 0)}
            position={{ lat: +x.lat, lng: +x.lng }}
            labelStyle={{ background: "#fff", border: "1px solid grey", padding: "2px 10px" }}

        >
            <Typography variant="button">{x.nombre}</Typography>
        </MarkerWithLabel>))}
    </GoogleMap>)
}));

function submitText(step) {
    if (step === 0) return "ENVIAR";
    if (step === 1) return "ENTREGADO";
    return step;
}