import React from "react";
import { TextField, InputAdornment, CircularProgress, Typography } from "@material-ui/core";
import { ReportProblem } from "@material-ui/icons";

var timeout;
export default function Input(props) {
    function onChange(event) {
        const { type, value } = event.currentTarget;
        props.onChange(event);
        var validated = true;
        if (type === "number") {
            validated = (value !== null && value !== undefined) &&
                value.toString().length
                && !isNaN(+value);
        }
        if (timeout) clearTimeout(timeout);
        if (props.onUpdate && validated) {
            timeout = setTimeout(() => {
                props.onUpdate(props.name, type === "number" ? parseFloat(value) : value)
            }, 1500);
        }
    }

    var fb = props.feedback[props.name];
    var error = fb && fb !== "loading";
    var visible_props = {
        InputProps: fb && (
            fb === "loading"
                ? {
                    endAdornment: (<InputAdornment>
                        <CircularProgress title="Guardando..." color="primary" size={20} />
                    </InputAdornment>)
                }
                : {
                    endAdornment: (<InputAdornment>
                        <ReportProblem color="secondary" />
                    </InputAdornment>)
                }
        )
    }
    if (error) {
        visible_props.helperText = <Typography variant="caption" color="secondary">{fb}</Typography>
    }
    return (
        <TextField
            type={props.type}
            style={{ marginTop: 10, marginBottom: 10 }}
            fullWidth
            onChange={onChange}
            value={props.value}
            label={props.label}
            name={props.name}
            multiline={props.multiline}
            inputProps={props.inputProps}
            {...visible_props}
            color={error ? "secondary" : "primary"}
        />);
}