import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import Login from './Login';
import API, { viewLoginData, storeLoginData } from "./Services/API";

function LaunchApp(data) {
    // uso viewLoginData porque pone por default la position en quito
    // en caso de que position sea NULL
    if (!data) data = viewLoginData();
    ReactDOM.render(<App user={data.user} store={data.store} onLogOut={LoggedOut} />, document.getElementById('app'));
}
function LaunchLogin() {
    ReactDOM.render(<Login onLogged={Logged} />, document.getElementById('app'));
}
function Logged(data) {
    storeLoginData(data);
    LaunchApp();
}
function LoggedOut() {
    API.STOP_LISTENING();
    storeLoginData();
    LaunchLogin();
}
//LaunchLogin();

viewLoginData()
    ? LaunchApp()
    : LaunchLogin();

