import React from "react";
import { Table, TableHead, TableRow, TableCell, TableBody, IconButton, CircularProgress } from "@material-ui/core";
import { Delete, Edit, Add } from "@material-ui/icons";
import API from "../../Services/API";
import HorarioDialog from "./HorarioDialog";

export default class extends React.PureComponent {
    state = {
        horarios: [],
        selected: null
    }
    constructor(props) {
        super(props);
        if (props.configuracion && props.configuracion.horarios) {
            this.state.horarios = [...props.configuracion.horarios];
        }
        this.handleDelete = this.handleDelete.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleEdit = this.handleEdit.bind(this);
        this.guardar = this.guardar.bind(this);
    }
    handleDelete(event) {
        const index = event.currentTarget.value;
        if (this.state.horarios.length <= 1) return alert("No se puede borrar: Debe existir al menos un horario");
        var item = this.state.horarios[+index];
        if (!item) return;
        if (item.loading) return;
        this.setState(prev => {
            const state = { ...prev };
            state.horarios = [...state.horarios];
            state.horarios[index].loading = true;
            return state;
        })
        const horarios = [...this.state.horarios];
        horarios.splice(index, 1);
        API.setConfigInfo({ horarios })
            .then(() => {
                this.setState({ horarios });
            }).catch(ex => {
                alert("Ocurrio un error: " + ex.message);
                this.setState(prev => {
                    const state = { ...prev };
                    state.horarios = [...state.horarios];
                    state.horarios[index].loading = false;
                    return state;
                });
            });
    }
    guardar(horario) {
        const horarios = JSON.parse(JSON.stringify(this.state.horarios));
        var exists;
        if (horario.id) exists = horarios.find(x => x.id === horario.id);
        if (exists) {
            horarios[horarios.indexOf(exists)] = horario;
        } else {
            horarios.push(horario);
        }
        return API.setConfigInfo({ horarios })
            .then(x => {
                this.setState({ horarios, selected: null });
            });
    }
    handleEdit(event) {
        const index = event.currentTarget.value;
        if (index === "NUEVO") return this.setState({ selected: "NUEVO" });
        this.setState(prev => {
            const state = { ...prev };
            state.horarios = [...state.horarios];
            var item = state.horarios[+index];
            if (!item) return state;
            state.selected = item;
            return state;
        });
    }
    handleClose() {
        this.setState({ selected: null });
    }
    render() {
        const { horarios, selected } = this.state;
        return (<div>
            {selected && <HorarioDialog data={selected} onClose={this.handleClose} guardar={this.guardar} />}
            <Table>
                <TableHead>
                    <TableRow>
                        <TableCell style={{ width: 40 }}>
                        </TableCell>
                        <TableCell component="th">HORA</TableCell>
                        <TableCell component="th">DIAS</TableCell>
                        <TableCell component="th" style={{ width: 40 }}>
                            <IconButton onClick={this.handleEdit} value="NUEVO" >
                                <Add color="primary" />
                            </IconButton>
                        </TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {horarios && horarios.map((item, index) =>
                        (<TableRow key={index}>
                            <TableCell scope="row" component="th">
                                <IconButton disabled={item.loading} value={index} onClick={this.handleDelete}>
                                    {item.loading ? <CircularProgress size={20} color="secondary" /> : <Delete />}
                                </IconButton>
                            </TableCell>
                            <TableCell>
                                {TimeToString(item.desde, item.tz) + " - " + TimeToString(item.hasta, item.tz)}
                            </TableCell>
                            <TableCell>
                                {item.dias.map(ToDayString).join(", ")}
                            </TableCell>
                            <TableCell>
                                <IconButton disabled={item.loading} value={index} onClick={this.handleEdit}>
                                    <Edit />
                                </IconButton>
                            </TableCell>
                        </TableRow>)
                    )}
                </TableBody>
            </Table>
        </div>)
    }
}


function ToDayString(d) {
    if (d === 1) return "Lun";
    if (d === 2) return "Mar";
    if (d === 3) return "Mie";
    if (d === 4) return "Jue";
    if (d === 5) return "Vie";
    if (d === 6) return "Sab";
    if (d === 0) return "Dom";
    return "UNDEFINED";
}
function TimeToString(minHour, tz) {
    const [hour, min] = minHour;
    return (
        (hour.toString().length === 1 ? "0" : " ") + hour
    ) + ":" + (
            (min.toString().length === 1 ? "0" : " ") + min
        )
}