import React from "react";
import { Drawer, List, ListItem, ListItemText, ListItemIcon, Divider } from "@material-ui/core";
import { Link } from "react-router-dom";
export default function (props) {
    const { STEPS, current_step } = props;
    return (<Drawer variant="permanent" style={{ maxWidth: 200 }}>
        <div style={{ paddingTop: 70, width: 200 }}>
            <List>
                {STEPS.map((step, index) => (<React.Fragment key={index}>
                    {step.addDividerBefore && <Divider />}
                    <ListItem
                        component={Link}
                        to={"/" + step.id}
                        selected={step.id === current_step}
                        button
                    >
                        <ListItemText>{step.title}</ListItemText>
                        <ListItemIcon>{<step.Icon />}</ListItemIcon>
                    </ListItem>
                </React.Fragment>
                ))}
            </List>
        </div>
    </Drawer>)
}